import { ReactNode, useEffect, useState } from 'react';
import { useRouter } from 'next/router';

type Role = 'admin' | 'vendor' | 'customer';

const decodePayload = (token: string): any => {
	try {
		const part = token.split('.')[1];
		const decoded = atob(part.replace(/-/g, '+').replace(/_/g, '/'));
		return JSON.parse(decoded);
	} catch (_) {
		return null;
	}
};

const normalizeRole = (raw?: string | null): Role | null => {
	if (!raw) return null;
	if (raw === 'user' || raw === 'customer') return 'customer';
	if (raw === 'admin') return 'admin';
	if (raw === 'vendor') return 'vendor';
	return null;
};

const getStoredRole = (): Role | null => {
	if (typeof window === 'undefined') return null;
	const token = localStorage.getItem('cc_token');
	const storedUser = localStorage.getItem('cc_user');

	// Prefer token payload
	if (token) {
		const payload = decodePayload(token);
		const tokenRole = normalizeRole(payload?.role);
		if (tokenRole) return tokenRole;
	}

	// Fallback to stored user object
	if (storedUser) {
		try {
			const parsed = JSON.parse(storedUser);
			const userRole = normalizeRole(parsed?.role);
			if (userRole) return userRole;
		} catch (_) {}
	}

	return null;
};

const defaultDashboard = (role: Role) => {
	if (role === 'admin') return '/admin/dashboard';
	if (role === 'vendor') return '/vendor/dashboard';
	return '/customer/dashboard';
};

interface ProtectedRouteProps {
	allowedRoles: Role[];
	children: ReactNode;
}

const ProtectedRoute = ({ allowedRoles, children }: ProtectedRouteProps) => {
	const router = useRouter();
	const [ok, setOk] = useState(false);

	useEffect(() => {
		if (typeof window === 'undefined') return;

		const token = localStorage.getItem('cc_token');
		if (!token) {
			router.replace('/login');
			return;
		}

		const role = getStoredRole();
		if (!role) {
			router.replace('/login');
			return;
		}

		if (!allowedRoles.includes(role)) {
			router.replace(defaultDashboard(role));
			return;
		}

		setOk(true);
	}, [allowedRoles, router]);

	if (!ok) return null;
	return <>{children}</>;
};

export default ProtectedRoute;
